@import 'components/ie7';
@import 'components/ie7-bootstrap';

.lt-ie8 {
    footer {
        .footer-social {
            .footer-social-txt {
                text-align: left;
            }

            .connect-with-us {
                display: block;
                position: static;
            }

            .footer-social-txt {
                position: static;
                right: auto;
                width: auto;
                white-space: normal;
            }
        }
    }

    .membership {
        .col-md-6 {
            width: auto;
        }

        .membership-menu {
            margin-bottom: 20px;

            text-align: left;
            border-right: none;

            a.membership-menu-item {
                padding: 10px;

                display: inline-block;
            }
        }

        .membership-items {
            border: none;
            padding: 0;
        }
    }

    .hero .hero-text {
        position: static;
        color: black;
        height: auto;
        .centered-text {
            display: block;
            max-height: none;
        }
    }

    .prices {
        .prices--header--desktop-only {
            display: none;
        }

        span.add--mobile--public,
        span.add--mobile--city-students,
        span.add--mobile--city-staff,
        span.add--mobile--other-students {
            color: black;
            font-weight: normal;
            display: inline;
            width: auto;
            height: auto;
            position: static;
            margin: auto;
            padding: 0;
            overflow: hidden;
            clip: auto;
            border: 0;
        }

        .prices--class-name {
            font-weight: bold;
        }

        .prices--items {
            border: 1px solid #e7e7e7;
        }

        .prices--table,
        .cell {
            border: none;
        }
    }
}
