/** column fix **/

@mixin boxsizefix() {
    border: 0;
    box-sizing: border-box;
    *behavior: url(/__data/assets/file/0007/245590/boxsizing.htc);
}

$column-names: ('xxs', 'xs', 's', 'sm', 'md', 'lg', 'xl', 'xxl');
$column-count: 24;
$column-numbers: ();
$selector: ();

@each $c in $column-names {
    @for $i from 1 through $column-count {
        $column-numbers: $column-numbers, unquote('.col-#{$c}-#{$i}');
    }
}

@each $item in $column-numbers {
    $selector: $selector, unquote('#{$item}');
}

.input-group,
.row,
.content,
#{$selector} {
    @include boxsizefix;
}

/** other fixes **/

audio,
canvas,
video {
    display: inline;
    zoom: 1;
}

html {
    font-size: 100%;
}

img {
    width: auto;
    height: auto;
    -ms-interpolation-mode: bicubic;
}

button,
input {
    overflow: visible;
}

.container,
.container-fluid {
    zoom: 1;
}

.row {
    zoom: 1;
}

.dl-horizontal {
    zoom: 1;
}

input[type='radio'],
input[type='checkbox'] {
    margin-top: 0;
}

.help-block {
    display: inline;
    zoom: 1;
}

.form-horizontal .form-group {
    zoom: 1;
}

.dropdown-toggle {
    margin-bottom: -3px;
}

.dropdown-menu .divider {
    width: 100%;
    margin: -5px 0 5px;
}

.list-group {
    margin-left: 0;
}

ul .list-group-item {
    list-style: none;
}

.sr-only {
    clip: rect(0 0 0 0);
}

.list-inline > li {
    display: inline;
    zoom: 1;
}
